import http from '@/api/http.js'
export default{
	data(){
		return{
			list:[],
			version:'1.0.0'
		}
	},
	onLoad() {
		this.getList()
		let version = uni.getStorageSync('version')
		this.version = version
	},
	methods:{
		getList(){
			http('/api/Setting/getAboutUs','post').then(res=>{
				this.list = res.data.data
				console.log(res)
			})
		},
		toAboutDetail(id){
			uni.navigateTo({
				url:'/pages/mine/set/aboutDetail?id='+id
			})
		}
	}
}
